.app-content {
  margin: auto;
  text-align: start;
  width:80%;
  padding-top: 60px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.changelog-item {
  margin-bottom: 20px;
}

.item-titlebar {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: normal;
  align-items: normal;
  align-content: normal;
  font-weight: bold;
  margin-bottom:8px;

}

.item-icon {
  display: block;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: auto;
  align-self: auto;
  order: 0;
}

.item-title {
  display: block;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: auto;
  align-self: center;
  order: 0;
  margin-left: 8px;
  font-weight:700;
  font-size:28px;
}

.item-subtitle {
  display: block;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: auto;
  align-self: center;
  order: 0;
  margin-left: 8px;
  font-weight:700;
  font-size:16px;
}

.item-changelist {
  padding-left: 30px;
}

.item-sub-icon {
  display: block;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: auto;
  align-self: auto;
  order: 0;
}

.item-titlebar-sub {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: normal;
  align-items: normal;
  align-content: normal;

}

.item-changelist {
  display: flex;
  flex-direction:column;
  margin-bottom:8px;
}

.item-desc {
  font-style: oblique;
  font-size: 12px;
  margin-bottom:4px;
  margin-left: 0px;
}

.change-before {
  color: #a0a0a0;
}

.change-name {
  font-style:normal;
}

.arrow {
  font-size: 80%;
}

#app-titlebar {
  color: #0D1016;
  position:fixed;
  background-color:#f2f2f2;
  height: 36px;
  width: 100%;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -webkit-justify-content: flex-start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-align-content: stretch;
  -ms-flex-line-pack: stretch;
  align-content: stretch;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  box-shadow: 10px 10px 10px #0D1016;
}

#app-title {
  font-size: 24px;
  font-weight: bolder;
  padding-left: 5%;
  -webkit-order: 0;
  -ms-flex-order: 0;
  order: 0;
  -webkit-flex: 1 1 auto;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
}

#titlebar-app-version {
   padding-right: 2%;
}